* {
  font-family: 'Jost', sans-serif;
}
.app_wrapper {
  display: grid;
  grid-template-columns: 2fr 10fr;
  grid-template-rows: 50px 1fr;
  grid-template-areas: 
  'header header'
  'nav content';
  grid-gap: 15px ;
  height: 100vh;


}

.app_wrapper_content {
  grid-area: content;
  background-color: lightblue;
  border-radius: 20px;
}

